@import url(https://fonts.googleapis.com/css2?family=Abel&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;

}
/* html{
  font-size: 62.5%;
} */
a{
  text-decoration: none;
  text-underline:none;
  color: white;
}

body{
  /* background-color:#191a1d; */
  background-color:#FFFFFF;
  scroll-behavior:smooth;
  color:white;
  font-family: "Oxygen" , sans-serif;
  font-weight: 300;
  box-sizing: border-box;

}

h1{
  font-size: 5rem;

}

h2{
  font-size: 1.7rem;
}

.flex-c{
  display: flex;
  justify-content: center;
  align-items:center;
}


body::-webkit-scrollbar {
  width: .2em;
}

body::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}
body::-webkit-scrollbar-thumb {
background-color: darkgrey;
outline: 1px solid slategrey;
}

@media (max-width: 800px) {

html{
  font-size: 14px;
}
}

.header{position:fixed;top:0;left:0;width:100%;display:flex;justify-content:space-between;align-items:center;padding:1rem;z-index:10}.header .logo{width:15rem;cursor:pointer;display:flex;justify-content:center;align-items:center;font-family:"Abel", cursive;color:black;top:1.2rem;left:1rem}.header .logo-line{width:3px;height:2.1rem;background-color:crimson;margin:0 .5rem;border-bottom-left-radius:10px;border-top-left-radius:10px}.header .logo-title{font-size:1.7rem;letter-spacing:1px}

.nav-slide{position:fixed;top:0;left:0;height:100vh;width:100%;background:white;display:flex;align-items:center;justify-content:center;z-index:20;transition:all .4s ease-out;transform:translateY(-100%);opacity:0}.nav-slide .page-links{display:flex;flex-direction:column;position:relative;align-items:flex-start}.nav-slide .page-links hr{width:50%;margin:1rem}.nav-slide .link-page{width:100%;margin:0 1rem;font-family:'Bebas Neue', cursive;font-size:4rem;font-weight:400;font-style:normal;opacity:0;cursor:pointer;color:black}.nav-slide .link-page:focus{outline:none}.nav-slide .link-page-open{-webkit-clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 80%);clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 80%);transform:translateY(-50px);opacity:0;animation-name:titleAnimation;animation-timing-function:ease;animation-delay:.5s;animation-duration:.5s;animation-fill-mode:forwards}@keyframes titleAnimation{0%{transform:translateY(-50px);opacity:0;-webkit-clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 80%);clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 80%)}100%{transform:translateY(0);opacity:1;-webkit-clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 15%);clip-path:polygon(100% 0, 100% 100%, 0 100%, 0 15%)}}.nav-slide .social-icon{width:2rem;height:2rem;background-color:white}

.burger-container{height:2rem;width:2rem;position:fixed;top:1.2rem;right:2rem;z-index:100;cursor:pointer;display:flex;flex-direction:column;justify-content:space-around;transition:all .3s;background-color:transparent;outline:none;border:none}.burger-container .burger-line1,.burger-container .burger-line2,.burger-container .burger-line3{width:1.9rem;height:1.3px;background-color:black;transition:transform .5s;transform:rotate(0deg);transform-origin:top right}.active .burger-line1{transform:rotate(-44deg);transform-origin:top right}.active .burger-line2{transform:translate(-1.5rem);opacity:0}.active .burger-line3{transform:rotate(44deg);transform-origin:bottom right}


.hero{height:100vh;width:100%;overflow:hidden}.hero-img{background-size:cover;background-image:url("https://res.cloudinary.com/dmhwdtooa/image/upload/v1634716915/photos/000052_pt3sla_rw6uor.jpg");background-position:center;width:100%;height:100%;animation:heroAnim 15s ease 1s forwards}

.slider-container{width:100%;height:100vh;display:flex;justify-content:center;align-items:center;cursor:pointer}.slider-container .slide{width:50rem;height:30rem;background-size:cover;background-position:center;display:flex;justify-content:center;align-items:center;position:relative}.slider-container .title{font-family:'Abel', sans-serif;position:absolute;z-index:-1;top:8rem;left:-6.7rem;font-size:6rem;letter-spacing:1px;color:#fff}.slider-container .title-border{font-family:'Abel', sans-serif;position:absolute;top:8rem;left:-6.7rem;font-size:6rem;-webkit-text-stroke-width:1px;-webkit-text-stroke-color:#191a1d;letter-spacing:1px}.slider-container .title,.slider-container .title-border{transition:all .5s ease-out;transform:translateX(-10rem);opacity:0;transition-delay:.5s}.slider-container .container-subtitile{display:flex;justify-content:center;align-items:center;position:absolute;top:16.5rem;left:-6.2rem;opacity:0;transform:translateX(-10rem);transition:all .5s ease-out;transition-delay:.7s}.slider-container .subtitle{background-color:#191a1d82;font-size:1.4rem;padding:0rem 1rem 0.2rem 1rem;margin-left:1rem;line-height:1.35rem}.slider-container .redline{width:0;height:2px;background-color:crimson;transition:all .6s ease-out;transition-delay:.7s}.slider-container .reveal .title,.slider-container .reveal .title-border,.slider-container .reveal .container-subtitile{opacity:1;transform:translateX(0)}.slider-container .reveal .redline{width:2rem}@media (max-width: 1025px){.slider-container .title,.slider-container .title-border{left:-4rem}.slider-container .container-subtitile{left:-2.5rem}}@media (max-width: 800px){.slider-container .title,.slider-container .title-border{left:-2rem}.slider-container .container-subtitile{left:-1.5rem}}@media (max-width: 600px){.slider-container .slide{width:100%;height:100%}.slider-container .title,.slider-container .title-border{font-size:5rem;left:1rem;top:42%;color:white;-webkit-text-stroke-color:white}.slider-container .container-subtitile{left:1.5rem;top:56%}}



.img{width:100%;background-size:cover;height:100%;background-position:center;cursor:zoom-in}.responsive{height:30vh}

.portfolio-container{margin:5rem .7rem 2rem .7rem;display:grid;-webkit-grid-template-columns:repeat(auto-fill, minmax(20rem, 1fr));grid-template-columns:repeat(auto-fill, minmax(20rem, 1fr));grid-gap:.7rem}

.about{height:100vh;width:100%;padding:9rem 4rem 0 4rem;position:relative;display:flex;overflow:hidden}.about .letters{font-size:12.5rem;position:absolute;color:rgba(255,255,255,0.449);z-index:-100;font-weight:900;top:5rem;font-weight:900;font-family:"Exo";transform:translate(-110%);transition:all 2s ease-in-out}.about .backround{position:absolute;z-index:-101;top:0;left:0;height:100%;width:100%;background-color:#9f9f9f}.about-img{height:100%;opacity:0.99;background-size:contain;background-position:center;background-repeat:no-repeat;transform:translateY(20%);transition:all .5s;opacity:0}.about-text,.about-image-area{height:100%;flex-direction:column}.about-text{align-items:flex-start;color:#252323;width:40%;transform:translateY(10%);transition:all .8s;transition-delay:1s;opacity:0}.about-text p{margin-top:1rem;font-size:1.1rem;word-spacing:.2rem;text-align:justify}.about-text h2{font-weight:900}.about-image-area{position:relative;width:60%;align-items:flex-end}.about-title{position:absolute;color:white;padding:3rem 10rem;font-size:5rem;bottom:0rem;left:-11rem}.about .about-social{margin-top:2rem}.about .react-icons{margin-left:1rem;width:2rem;height:2rem;vertical-align:middle;justify-content:center;align-items:center;color:#343333}.about .link-button{border:1px solid #343333;border-radius:2px;color:#525050;padding:0.5rem 1rem}.about .show{transform:translate(0%);opacity:1}@media (max-width: 1025px){.about{flex-direction:column-reverse;height:auto}.about .letters{font-size:7rem}.about-text,.about-image-area{width:100%;flex-direction:column}.about .about-text{margin:2rem;justify-content:flex-start;align-items:flex-start;color:#ffffff}.about-img{box-shadow:0 4px 15px -15px #565656;width:100%}.about .link-button{border:1px solid #ffffff;color:#ffffff}.about .react-icons{color:#ffffff}}@media (max-width: 600px){.about .letters{font-size:4rem}.about-text{margin:2rem;justify-content:flex-start;align-items:flex-start;color:#525050}.title{font-size:4rem}}

.contact{padding:7em 7rem 3rem 7rem;flex-direction:column;position:relative;height:100vh;color:grey}.contact-title{margin-bottom:2rem;font-weight:700;font-size:3.2rem;color:grey}.contact-content{width:100%;background:#FFFFFF;border-radius:3px}.contact .react-icons{width:1.5rem;height:1.5rem;vertical-align:middle}.contact-form{flex-direction:column;width:60%;padding:3rem 4rem}.contact-left{height:100%;width:50%;border-right:0.5px solid grey;font-size:1.5rem;padding:5rem 3rem}.contact-details h2{margin-bottom:2rem;font-size:2rem;color:#a0a0a0}.contact-details p{margin-top:1rem}.contact-button{margin-top:2rem;background-color:#0a0a0c00;width:10rem;height:3rem;color:grey;border-radius:3px;font-family:inherit;border:0.5px solid grey;cursor:pointer}.contact-backround{position:absolute;top:0;left:0;height:100%;width:100%;background-color:white;background-size:cover;background-position:center;opacity:0.2;z-index:-1}@media (max-width: 1023px){.contact-content{flex-direction:column}.contact-left{border:none;width:100%}.contact-form{flex-direction:column;width:100%;padding:3rem 4rem}}@media (max-width: 800px){.contact{height:auto}}@media (max-width: 600px){.contact{padding:5em 1rem 3rem 1rem;height:auto}.contact-left,.contact-form{height:auto;padding:2rem 1.5rem}.contact-title{font-size:3rem}}

.group{margin-top:1rem;width:100%}.group .form-input{background:none;color:grey;font-size:20px;padding:10px;display:block;width:100%;border:none;border-radius:3px;border:.5px solid grey;margin-top:10px;font-family:inherit;cursor:pointer}.group .form-input:focus{outline:none}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:white;font-size:13px;font-weight:normal;pointer-events:none;transition:300ms ease all}textarea{height:15rem;font-family:inherit}input::-webkit-input-placeholder, textarea::-webkit-input-placeholder{opacity:.5;font-size:1rem}input:-ms-input-placeholder, textarea:-ms-input-placeholder{opacity:.5;font-size:1rem}input::placeholder,textarea::placeholder{opacity:.5;font-size:1rem}input[type=number]::-webkit-inner-spin-button,input[type=number]::-webkit-outer-spin-button{-webkit-appearance:none;margin:0}input:focus::-webkit-input-placeholder, textarea:focus::-webkit-input-placeholder{color:transparent}input:focus:-ms-input-placeholder, textarea:focus:-ms-input-placeholder{color:transparent}input:focus::placeholder,textarea:focus::placeholder{color:transparent}input[type=number]{-moz-appearance:textfield}

